
import { _decorator, Component, Node, PhysicsSystem2D, EPhysics2DDrawFlags, Collider2D, Contact2DType, IPhysics2DContact, BoxCollider2D, director, Sprite, Color, resources, SpriteFrame, Layers, find, Prefab, instantiate, Vec2, ProgressBar, NodePool } from 'cc';
import { BulletPerManeger } from './BulletPerManeger';
import { combatConfigManeger } from './combatConfigManeger';
import { livingSystemManeger } from './livingSystemManeger';
import { targetManeger } from './targetManeger';
const { ccclass, property } = _decorator;

@ccclass('DanPerManeger')
export class DanPerManeger extends Component {
    
    public bulletArray = [];
    public hasEmeny = true;

    public bulletPool:NodePool = new NodePool();

    public targetArray:Array<Node> = new Array<Node>

    @property(livingSystemManeger)
    DanLivSysMag:livingSystemManeger = null;

    public currentLivingValue:number;
    onLoad(){
        // director.enable = true;
        //绘制物理信息
        PhysicsSystem2D.instance.enable = true;
        PhysicsSystem2D.instance.debugDrawFlags = EPhysics2DDrawFlags.Aabb | 
            EPhysics2DDrawFlags.Pair | 
            EPhysics2DDrawFlags.CenterOfMass |
            EPhysics2DDrawFlags.Joint |
            EPhysics2DDrawFlags.Shape ;
    }

    start(){
        this.initialDan();
        // 批量生成子弹
        let that = this;
        let flag = false;
        this.schedule(function(){
            that.hasEmeny = that.node.parent.getComponent(targetManeger).hasEmeny;
            if(that.hasEmeny){
                that.targetArray = that.node.parent.getComponent(targetManeger).emenyArray;
                flag = false;
                for(let i =0;i<that.targetArray.length;i++){
                    if(that.targetArray[i].isValid){
                        flag = true;
                    }
                }
                if(!flag){
                    that.hasEmeny = false;
                    that.node.parent.getComponent(targetManeger).hasEmeny = false;
                }
                console.log('createBullet')
                that.createBullet();
            }
        },1)
        
        //注册单个碰撞体的回调函数
        // console.log('tool information')
        let collider = this.node.getComponent(Collider2D);
        // console.log(collider);
        // console.log(collider);
        if (collider) {
            collider.on(Contact2DType.BEGIN_CONTACT, this.onBeginContact, this);
            collider.on(Contact2DType.END_CONTACT, this.onEndContact, this);
            collider.on(Contact2DType.PRE_SOLVE, this.onPreSolve, this);
            collider.on(Contact2DType.POST_SOLVE, this.onPostSolve, this);
        }
    }
    
    onBeginContact (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {


        this.underFire(otherCollider)
    }
    onEndContact (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 只在两个碰撞体结束接触时被调用一次
        // console.log('PAO onEndContact');
        // this.hasEmeny = false;


    }
    onPreSolve (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 每次将要处理碰撞体接触逻辑时被调用
        console.log('PAO onPreSolve');
    }
    onPostSolve (selfCollider: Collider2D, otherCollider: Collider2D, contact: IPhysics2DContact | null) {
        // 每次处理完碰撞体接触逻辑时被调用
        // console.log('PAO onPostSolve');

    }

    update(deltaTime: number) {
        console.log(this.bulletArray.length);
        //获取攻击速度
        let speed = this.DanLivSysMag.attackSpeed;
        for(let i=0;i<this.bulletArray.length;i++){
            let bulletNode = this.bulletArray[i];
            let istarget = bulletNode.getComponent(BulletPerManeger).istarget;
            let isOverScreen = bulletNode.getComponent(BulletPerManeger).isOverScreen;
            // console.log("是否命中",istarget)
            if(istarget || isOverScreen){
                // bulletNode.destroy()
                this.bulletPool.put(bulletNode);
            }

            let y = bulletNode.position.y;
            let x = bulletNode.position.x;
            x = x - speed * deltaTime;
            y = y + speed * deltaTime;
            bulletNode.setPosition(x,y,0)
            //TODO:出屏幕后销毁
        }
    }
    destroyThisNode(){
        this.node.destroy()
    }
    initialDan(){
        let AttackSpeed = find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).ZhuiZongDan.AttackSpeed;
        let LivingValue = find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).ZhuiZongDan.LivingValue;
        let DefendValue = find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).ZhuiZongDan.DefendValue;
        let HarmValue = find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).ZhuiZongDan.HarmValue;

        this.DanLivSysMag = new livingSystemManeger(LivingValue,HarmValue,DefendValue,2,AttackSpeed);
        // let that = this;
        // resources.load("sanshe/SpriteFrame",SpriteFrame,(err,SpriteFrame)=>{
        //     if (!SpriteFrame) {
        //         console.log('散射炮精灵图为空')
        //     }else{
        //         console.log('散射跑精灵图加载完成')
        //         this.node.getComponent(Sprite).spriteFrame = SpriteFrame;
        //         this.no
        //     }
        // })
        console.log("散射炮初始化成功")
    }
    underFire(Collider: Collider2D){
        
        let harmedValueEmeny=find("Canvas/GlobalRoundControl").getComponent(combatConfigManeger).EmenyNormal.HarmValue;
        let defendValue = this.DanLivSysMag.defendValue;

        if(Collider.node.name == "Bullet_Pre"){
            // console.log("我军受到攻击")
            if(this.currentLivingValue - harmedValueEmeny*(1-defendValue) <= 0){
                this.node.parent.destroy()
            }else{
                this.currentLivingValue -=harmedValueEmeny*(1-defendValue)
                // console.log("生命值为：",this.ZhuiZongDanLivSysMag.livingValue)
                this.node.parent.getChildByName("ProgressBar").getComponent(ProgressBar).progress = 0.5 * (this.currentLivingValue/this.DanLivSysMag.livingValue)
            }
        }
        
    }

    //生成子弹
    createBullet(){
        // let node = new Node();
        // node.addComponent(Sprite);
        let that = this;
        resources.load("perfabs/PaoBulletPer",Prefab,(err,Prefab)=>{
            if(!Prefab){
                console.log('子弹预制体为空')
            }else{
                console.log('预制体制作完成')
                let node = instantiate(Prefab);
                if(!node){
                    console.log('没有节点')
                }else{
                    // console.log(node)
                    // let pos = new Vec2();
                    node.layer = Layers.Enum.UI_2D;
                    let x = that.node.position.x + 400;
                    let y = that.node.position.x + 225;
                    
                    // let shit = pos.set(x,y)
                    // x = shit.x - 800;                   
                    // y = shit.y - 450;
                    console.log(x,y)
                    node.setPosition(0,0,0);
                    that.node.addChild(node);
                    that.bulletArray.push(node);
                }
                
            }
        })

        
        
        // resources.load('zidan1/spriteFrame',SpriteFrame,(_err,sproteFarme)=>{
        //     node.getComponent(Sprite).spriteFrame = sproteFarme;
        //     node.layer = Layers.Enum.UI_2D;
        //     console.log(find("left"))
        //     console.log(this.node)
        //     node.parent = this.node;
        //     console.log(node);
        //     // node.parent = find("left").getChildByName("rightUp").getChildByName("pao")
        // })
        
    }
}




